  <div class="header-container">
<b-container class="no-margin">
    <b-row align-h="center">
        <b-col><h1 class="p-color">g</h1></b-col>
        <b-col><h1 class="p-color">l</h1></b-col>
        <b-col><h1 class="p-color">i</h1></b-col>
