    </b-row>
</b-container>
<b-container class="middle-margin">
  <b-row>
  <b-col>
<p>Glide is one of my final projects for this year. In 4th year we do a capstone project, which means we have 1 class that works on this and only this. This year we started with a prompt. Our profs gave us 4 possible prompts to choose from. They were courtesy of the RSA in London.</p>
<dl>
  <dt>Alone Together</dt>
  <dd>Accounting for how different groups in society are affected by loneliness, design a way to reduce its impact on one or more of the following: physical health, mental health, or access to services</dd>
<dt>Citizens as Shapers</dt>
<dd>Design a solution that harnesses digital tools to increase the quantity and quality of citizens’ participation in democratic processes. </dd>
<dt>Harvesting Health</dt>
<dd>Design a product, system or service which uses sustainable food and farming to help improve people’s health or wellbeing. </dd>
<dt>Hidden Figures</dt>
<dd>Design a way to break down the physical, organizational or attitudinal barriers that people with hidden disabilities or impairments can face in society, to enable them to live their lives to the full. </dd>
</dl>
<p>Our team picked “Hidden Figures.” I was picked as the Project Manager and we began with research. We very quickly realized that we need to pick a specific hidden disability to focus on. After some debate and research, we picked Autism Spectrum Disorder (ASD). Since then we have had a serious focus on research. This disorder has a lot of factors that affect it. One of the first things we noted was language. As people researching this for the first time, we knew nothing. There was a huge scope of knowledge just concerning how to talk about and with people with ASD. Not only do different people prefer different things, but there isn’t much of a consensus on the language. It took us several weeks just to learn this. One of our primary tasks was also research on ASD. We wanted to learn how it affected people, and what people did to enable themselves. </p>
<p>Our research took different stages, we did online research, by joining Facebook groups and forums, we emailed and went out in the field, we found friends and family who we could talk to. One of our biggest problems was finding people to talk to. </p>
<p>After weeks and weeks of research, we started to ideate. We did this in different forms, but mainly in the “8 ideas, 8 minutes” form. We compiled all of our ideas, and broke them down, we figured out which ones made sense, and which didn’t. We also continued to research by looking at apps, services, and tools that exist already. </p>
<p>After much deliberation, we had 3 possible ideas. </p>
<ol>
  <li>A journaling app to help build socialization skills</li>
  <li>A case for iPhones and iPads that had a customizable stimulant on the back. </li>
  <li>An app that helps people find a backup plan when their schedule is broken. </li>
</ol>
<p>We picked the last one. Our team felt that it was the most applicable and most challenging one to design and create. </p>
<p>We observed that many people with ASD like to schedule their days, this can be in different forms and levels of complexity. But when something goes wrong, this can lead to shutdown or overload for some people. This might mean that they need to take an hour-long break, or their entire day is ruined. It is different for every person. </p>
<p>We started designing some basic elements of our app and understanding how it will work. We came up with the concept “Glide: helping you move through your day smoothly.” And concept based on paper airplanes. </p>
<p>Our next step was understanding how to make this app work. </p>
<p>We are trying to give backup plans for when something goes wrong in a person’s schedule. </p>
<p>This can range from “missing a bus” to “my friend ditched me” or “my internet is out.” We had no clue how to service this. </p>
<p>Firstly we decided to create some Personas to help guide us. </p>
<p>For each of the Personas, we also create a schedule. </p>
<p>We realized that this task isn’t easy. How would we understand what is going wrong in a person’s schedule?</p>
<p>So we came up with a method. When someone creates an input such as, “Lunch with Patrick at the Robotics Lounge.” </p>
<p>The several inputs are the person, the place, and the task. </p>
<p>This means we can prompt to ask if something is wrong with those three and gather information from that. </p>
<p>We then designed a flowchart example of things going wrong. When doing this we noticed several more issues, sometimes we won’t be able to figure out the actual problem, and that having a “dismiss and call a friend” button on the screen at all times was important. We know that we won’t solve every problem, but we’re trying to make the transition easier for people. We want to avoid as many shutdowns or overloads as possible and if not avoid at least reduce the severity. </p>
<p>As of now, we have created some mockups and are set into the test phase. We need to know if this works, if this solves a problem, and if it helps. </p>
 </b-col>
</b-row>
