  <h2><a class="link" target="_blank" href="http://fashionforchange.ca">Check out the new site!</a></h2>
 <h4><span class="heading5">Introduction</span></h4>
<p>I joined Fashion for change in 2017 and stayed with the team until the end of the school year. I stayed on during my exchange, where I would have to be up at all times of the night to make our meetings. </p>
 <h4><span class="heading5">About</span></h4>
 <p>Fashion for Change is a non-profit organization based at the University of Waterloo and Wilfred Laurier University. It’s the largest student-run charity in the Kitchener - Waterloo region. They raise up to $20,000 the WE Villages Program. </p>
  <h4><span class="heading5">Goals</span></h4>
  <p>In November of 2017, I was hired onto their design team to work on a re-design of their website. Their old website was designed over 5 years ago. It didn’t adapt to different screen sizes, needed a branding and general refresh. Our team of 3 was tasked with re-designing the user experience, the general design, the job application process, and improve sponsor information.</p>
  <h4><span class="heading5">My Role</span></h4>
<p>I was hired on as the User Experience Designer. As part of my role, I worked on improving the site architecture, the user experience, and I introduced the initial design feel that we ended up implementing. </p>
  <h4><span class="heading5">Old Site Review</span></h4>
  <p>n/a</p>
    <h4><span class="heading5">Users and Personas</span></h4>
    <p>As part of our research and strategy, we designed several different user personas. As you can see these ranged in the type of person we are trying to target, from students to sponsors. </p>
</b-container>
