  </b-col>
</b-row>
      <h4><span class="heading5">New Site Organization</span></h4>
      <p>The first step to our redesign we creating a new site organization. We needed to reorganize the old version. It was too complicated. We aimed to have several tabs at the top with more information falling into the pages. </p>
        <h4><span class="heading5">Design Stages</span></h4>
        <p>The design of the website went through several iterations. We started with concepts and move towards a more streamlined design. I would say my designs were very creative and I really tried to break the mold with them. I encouraged us to do a more landing page style for the pages as they would be able to give information and attract our target the best. </p>
<h2><a class="link" href="http://fashionforchange.ca">Check out the new site!</a></h2>
